import React, { useEffect, useState } from "react";
import { createRoot } from "react-dom/client";
import { APIProvider, Map } from "@vis.gl/react-google-maps";
import { DeckGlOverlay } from "./deckgl-overlay";
import ControlPanel from './control-panel';
import { GeoJsonLayer } from "@deck.gl/layers/typed";
import type { Feature, GeoJSON } from "geojson";

const App = () => {
  const [stationsData, setStationsData] = useState<GeoJSON | null>(null);
  const [ridesData, setRidesData] = useState<GeoJSON | null>(null);

  useEffect(() => {
    const load = async () => {
      await Promise.all([
        fetch("https://raw.githubusercontent.com/spudtrooper/spudtrooper.github.io/main/citibikerides/rides-geojson.json")
          .then(res => res.json())
          .then(data => setRidesData(data as GeoJSON)),
        fetch("https://raw.githubusercontent.com/spudtrooper/spudtrooper.github.io/main/citibikerides/stations-geojson.json")
          .then(res => res.json())
          .then(data => setStationsData(data as GeoJSON)),
      ]);
    };
    load();
  }, []);

  const getColor = (n: number): [number, number, number, number] => {
    const b = Math.floor(255 * (1 - n));
    const g = Math.floor(255 * n);
    const a = 255 - b;
    return [b, g, b, a];
  }

  function getDeckGlLayers() {
    if (!stationsData || !ridesData) return [];

    return [
      new GeoJsonLayer({
        id: "stations-geojson-layer",
        data: stationsData,
        stroked: false,
        filled: true,
        extruded: true,
        pointType: "circle",
        getFillColor: [160, 160, 180, 200],
        getLineColor: [255, 0, 0, 255],
        getPointRadius: 50,
        getDataComparator: false,
        getElevation: 0,
      }),
      new GeoJsonLayer({
        id: "rides-geojson-layer",
        data: ridesData,
        stroked: false,
        filled: true,
        extruded: true,
        pointType: "circle",
        lineWidthScale: 20,
        lineWidthMinPixels: 4,
        getLineColor: (f: Feature) =>
          getColor(f?.properties?.normalizedRideCount),
        getDataComparator: false,
        getElevation: 0,
      }),
    ];
  };

  return (
    <APIProvider apiKey={""}>
      <Map
        defaultCenter={{ lat: 40.76696, lng: -73.98179 }}
        defaultZoom={11}
        mapId={"4f6dde3310be51d7"}
        gestureHandling={"greedy"}
        disableDefaultUI={true}>
        <DeckGlOverlay layers={getDeckGlLayers()} />
        <ControlPanel />
      </Map>
    </APIProvider>
  );
};

export default App;

export function renderToDom(container: HTMLElement) {
  const root = createRoot(container);

  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}
